.container-grid {
  display: grid;
  column-gap: 0.75rem;
  row-gap: 0.5rem;
  grid-template-columns: 1fr;
  grid-template-rows: 1.25rem 2.5rem 0.75rem 1.25rem 2.5rem 0.75rem 1.25rem 10rem 0.75rem 0.75rem auto;
  grid-template-areas:
    "label_name"
    "field_name"
    "error_name"
    "label_email"
    "field_email"
    "error_email"
    "label_message"
    "field_message"
    "error_message"
    "buttonbar";
}
.container-grid .label {
  justify-self: start;
}
.container-grid .label-name {
  grid-area: label_name;
}
.container-grid .label-email {
  grid-area: label_email;
}
.container-grid .label-message {
  grid-area: label_message;
}
.container-grid .field-name {
  grid-area: field_name;
}
.container-grid .field-email {
  grid-area: field_email;
}
.container-grid .field-message {
  grid-area: field_message;
}
.container-grid .error-name {
  grid-area: error_name;
}
.container-grid .error-email {
  grid-area: error_email;
}
.container-grid .error-message {
  grid-area: error_message;
}
.container-grid .status-message {
  grid-area: status;
}
.container-grid .buttonbar {
  grid-area: buttonbar;
  place-self: center end;
}
.container-grid .buttonbar button {
  margin-left: 1rem;
}
@media (min-width: 768px) {
  .container-grid {
    grid-template-columns: 6rem 1fr;
    grid-template-rows: 2.5rem 0.75rem 2.5rem 0.75rem 10rem 0.75rem 0.75rem auto;
    grid-template-areas:
      "label_name field_name"
      ". error_name"
      "label_email field_email"
      ". error_email"
      "label_message field_message"
      ". error_message"
      "buttonbar buttonbar";
  }
  .container-grid .label {
    justify-self: end;
  }
}
